.abonnements {
  width: 100%;
  position: relative;
}
.abonnements__item-sale {
  position: absolute;
  top: 4px;
  right: 9px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  background-image: url(../img/Star.svg);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center;
  font-family: "pt-root-ui", "Arial", sans-serif;
  font-weight: 500;
  font-size: 13px;
  line-height: 130%;
  color: #FFFFFF;
}
@media (min-width: 1280px) {
  .abonnements__item-sale {
    width: 70px;
    height: 70px;
    right: 5px;
    top: -30px;
    font-size: 18px;
  }
}
.abonnements__item-sale--animation {
  animation: sale 1500ms infinite;
}
@keyframes sale {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
.abonnements__list {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 1280px) {
  .abonnements__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 12px;
    row-gap: 40px;
  }
}
.abonnements__item {
  width: 100%;
  height: 100%;
  position: relative;
}
@media (min-width: 1280px) {
  .abonnements__item--all_width {
    grid-column-start: 1;
